/*电话绑定*/
body {
  background: #fff;
}
#bindTel {
  padding: 0.2rem;
  padding-top: 38px;
}
#bindTel .yd-cell-item.telHang,
.code {
  background: #f5f5f5;
  border: 1px solid #CECECE;
  border-radius: 0.1rem;
  height: 0.9rem;
  line-height: 0.9rem;
}
#bindTel .yd-cell-item.codeHang {
  padding: 0;
}
#bindTel .yd-cell-item.codeHang .yd-cell-left {
  /*flex:2;
  */
  min-width: 65%;
}
#bindTel .yd-cell-item.codeHang .yd-cell-right {
  max-width: 30%;
  margin-left: 5%;
  padding-right: 0;
}
#bindTel .yd-cell-item.codeHang .yd-cell-right .button {
  width: 100%;
}
.code {
  width: 100%;
  padding-left: 0.24rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#bindTel .telHang {
  margin-bottom: 0.16rem;
}
#bindTel .yd-cell-left {
  color: #242424;
  font-size: 0.28rem;
}
#bindTel .getCode {
  min-width: 100%;
  background: #3375C5;
  height: 0.9rem;
  line-height: 0.9rem;
  color: #fff;
  font-size: 0.28rem;
  border-radius: 0.1rem;
  padding: 0;
}
#bindTel .yd-cell:after {
  display: none;
}
#bindTel .telInput input,
.code input {
  padding-left: 20px;
  color: #3375C5;
  font-size: 14px;
}
.code input {
  padding-left: 10px;
}
.tips {
  color: #8a8a8a;
  font-size: 11px;
  margin-top: 26px;
}
.tips .iconfont {
  color: #F83345;
  font-size: 15px;
  margin-right: 7px;
}
